
import { defineComponent } from "vue";

export default defineComponent({
   name: "Image",
   props: {
      source: { default: "", type: String },
      noHave: { default: true, type: Boolean },
      locationNum: { default: 0, type: Number },
      rowNum: { default: 1, type: Number },
      pikminNum: { default: 1, type: Number },
   },
   emits: ["emitNoHave"],
   setup(props, context) {
      const changeColor = () => {
         context.emit(
            "emitNoHave",
            !props.noHave,
            props.locationNum,
            props.rowNum,
            props.pikminNum
         );
      };

      return {
         changeColor,
      };
   },
});
