
import { defineComponent, reactive, toRefs } from "vue";

export default defineComponent({
   name: "Navbar",
   setup() {
      const state = reactive({
         thisPage: window.location.pathname,
      });

      const changePage = (page: string) => {
         state.thisPage = page;
      };

      const clearData = () => {
         if (confirm("是否清除資料") === true) {
            window.localStorage.removeItem("pikminData");
            location.reload();
         }
      };

      return {
         ...toRefs(state),
         changePage,
         clearData,
      };
   },
});
