
import { defineComponent, reactive, toRefs } from "vue";
import Image from "@/components/Image.vue";
import { location, pikmin, initData } from "@/data/init";

interface initDataClass {
   [index: number]: locationClass;
}

interface locationClass {
   location: string;
   class: [Object: pikminClass];
   total: number;
}

interface pikminClass {
   color: string;
   noHave: boolean;
}

export default defineComponent({
   name: "Pikmin",
   components: {
      Image,
   },
   setup() {
      const state = reactive({
         color: pikmin,
         location: location,
         pikmin: [] as initDataClass,
      });

      const changeColor = (
         noHave: boolean,
         locationNum: number,
         rowNum: number,
         pikminNum: number
      ) => {
         state.pikmin[locationNum].class[
            (rowNum - 1) * 4 + (pikminNum - 1)
         ].noHave = noHave;

         //計算數量
         state.pikmin[locationNum].total = state.pikmin[
            locationNum
         ].class.filter((e: pikminClass) => !e.noHave).length;

         window.localStorage.setItem(
            "pikminData",
            JSON.stringify(state.pikmin)
         );
      };

      if (window.localStorage.getItem("pikminData") === null) {
         state.pikmin = Object.assign([], initData);
      } else {
         const Data = JSON.parse(
            window.localStorage.getItem("pikminData") || "{}"
         );
         state.pikmin = Data;
      }

      return {
         ...toRefs(state),
         changeColor,
      };
   },
});
